@import "mixins";
@import "fonts";
@import "vars";
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
}

@media (min-width: 992px) {
  html,
  body {
    height: 100%;
  }
}

a {
  text-decoration: none!important;
  color: #000;
}

button:hover {
  cursor: pointer;
}

li {
  list-style: none;
}

.bold-text {
  font-weight: 700;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: $font-primary;
  @include rfs(15);
}

.main {
  padding-top: 20px;
  position: relative;
  flex: 1 0 auto;
  overflow: hidden;
  padding-bottom: 42px;
  box-sizing: border-box;
}

.container {
  @media (min-width: 320px) {
    max-width: 1240px;
  }
}

.content__festname {
  padding-top: 52px;
  text-align: center;
  img {
    max-width: 394px;
  }
}

.content-title {
  display: flex;
  margin: 16px auto 60px;
  padding: 10px 20px;
  @include rfs(28);
  letter-spacing: 5px;
  font-family: $font-kelly;
  color: #fff;
  text-transform: uppercase;
  background-color: #000;
}

.dark-yellow-title {
  display: inline-block;
  padding: 10px 10px 5px;
  margin: 0;
  margin-bottom: 15px;
  @include rfs(22);
  line-height: 1;
  letter-spacing: 2px;
  color: $yellow;
}

// day
.day {
  margin-bottom: 30px;
  .day__header {
    margin-bottom: 33px;
    display: flex;
    align-items: center;
    font-family: $font-kelly;
  }
  .day__number {
    position: relative;
    font-size: 69px;
    &:before {
      content: "";
      position: absolute;
      top: 5px;
      left: -16px;
      width: 65px;
      height: 64px;
      transform: rotate(-10deg);
      background-color: $yellow;
      z-index: -1;
    }
  }
  .day__date {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 24px;
    text-transform: uppercase;
  }
  .day__day {
    margin-bottom: 4px;
    font-size: 12px;
    text-transform: uppercase;
    color: $grey;
  }
  .day__numeric {
    margin-bottom: 8px;
    padding: 3.5px 4px 3.5px 5px;
    text-align: center;
    font-size: 15px;
    letter-spacing: 3px;
    color: #fff;
    background-color: #000;
  }
  .day__month {
    font-size: 22px;
    letter-spacing: 4px;
  }
}

// back to top
#backTop {
  display: flex;
  justify-content: center;
  width: 84px;
  height: 84px;
  position: absolute;
  bottom: -42px;
  left: 0;
  right: 0;
  margin: auto;
  font-size: 30px;
  color: #000;
  background-color: $yellow;
  border-radius: 50%;
  z-index: 1;
  span {
    margin-top: 10px;
  }
}

// social icons
.social-iconset {
  display: flex;
}

.social-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 21px;
  height: 21px;
  margin-right: 7px;
  margin-left: 7px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .3s ease;
  .icon {
    color: #000000;
    font-size: 16px;
  }
  &:hover {
    background-color: lighten($yellow, 20);
  }
}

// navigation
.nav-item {
  margin-left: 12px;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  transition: all .3s ease;
  a {
    color: #fff;
  }
  &:first-child {
    margin-left: 0;
  }
}

.nav-item.active a.nav-link {
  color: $yellow;
}

.nav-link {
  white-space: nowrap;
  font-size: 15px;
  &:hover {
    color: lighten($yellow, 20);
  }
}

// HEADER
.header {
  display: flex;
  align-items: center;
  min-height: 110px;
  background-color: #000;
  // logo
  .navbar-brand {
    max-width: 99px;
    min-width: 70px;
    margin-left: 12px;
    padding: 0;
    .header__logo {
      width: 100%;
      transition: transform .3s ease;
      &:hover {
        transform: scale(1.05);
      }
    }
  }
  .navbar {
    width: 100%;
    justify-content: space-between;
    color: #FFFFFF;
    // header menu
    .navbar-collapse {
      flex-basis: auto;
      @media only screen and (max-width: 992px) {
        flex-basis: 100%;
      }
    }
  }
  // header search
  .header__search {
    margin-left: 40px;
  }
  .navbar__search-btn {
    font-size: 20px;
    color: #fff;
    background-color: transparent;
    border: none;
    border-color: transparent;
  }
  // language switcher
  .header__lang {
    display: flex;
    align-items: center;
    margin-left: 11px;
    font-size: 10px;
    line-height: 1;
  }
  .header__lang-link {
    padding-left: 5px;
    padding-right: 5px;
    text-transform: uppercase;
    transition: color .3s ease;
    a {
      color: #fff;
    }
    &:hover a {
      color: $yellow;
    }
  }
  .header__lang-mk {
    border-right: 1px solid #fff;
  }
  .header__lang-link .active {
    color: $yellow;
  }
  // hamburger
  .icon-bars {
    display: inline-block;
    margin-left: 25px;
    margin-right: 25px;
    font-size: 25px;
    color: #fff;
  }
}

// HERO
.main--hero {
  padding-top: 0;
  padding-bottom: 0;
}

.hero {
  position: relative;
  width: 100%;
  min-height: calc(100vh - 110px);
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  .hero__magnifier {
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 0;
    height: 110vh;
    background-image: url("../img/hero-magnifier.png");
    background-position-x: center;
    background-size: contain;
    background-repeat: no-repeat;
    @media only screen and (max-width: 768px) {
      background-image: none;
    }
  }
  .hero__inner {
    padding-bottom: 24px;
    min-height: calc(100vh - 110px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media only screen and (max-width: 768px) {
      padding-top: 40px;
      padding-bottom: 60px;
      flex-direction: row;
    }
  }
  .hero__data {
    position: absolute;
    top: 7px;
    right: 6%;
    z-index: 10;
    @media only screen and (max-width: 768px) {
      position: static;
    }
  }
  .hero__poster {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    @media only screen and (max-width: 640px) {
      flex-direction: column;
      align-items: center;
    }
  }
  .hero__poster-inscription {
    width: 432px;
    max-width: 433px;
    min-width: 300px;
    @media only screen and (max-width: 480px) {
      width: 100%;
    }
    img {
      width: 100%;
    }
  }
  .hero__poster-moto {
    width: 36%;
    margin-top: 41px;
    margin-left: 32px;
    font-family: $font-kelly;
    line-height: 1.2;
    letter-spacing: 8px;
    @include rfs(34);
    line-height: 1.2;
    letter-spacing: 8px;
    @media only screen and (max-width: 640px) {
      margin-top: 5px;
    }
    span {
      display: inline-block;
      margin-bottom: 10px;
      padding-left: 5px;
      padding-right: 5px;
      white-space: nowrap;
      background-color: #fff;
      &:last-child {
        margin-left: 25px;
      }
    }
  }
  .hero__nav-wrap {
    width: 100%;
  }
  .hero__nav {
    display: flex;
    margin-top: 115px;
    justify-content: center;
    @media only screen and (max-width: 768px) {
      flex-wrap: wrap;
      margin-top: -100px;
    }
  }
  .hero__nav-item {
    width: 154.13px;
    height: 53.13px;
    margin-left: 27px;
    margin-right: 27px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $font-kelly;
    letter-spacing: 1.3px;
    color: $yellow;
    @include rfs(20);
    text-transform: uppercase;
    background-image: url("../img/link-bg.svg");
    background-size: cover;
    background-repeat: no-repeat;
    transition: color .2s ease;
    @media only screen and (max-width: 768px) {
      margin-top: 20px;
    }
    &:hover {
      color: #fff;
    }
    &:first-child {
      margin-left: 0;
    }
  }
}

// Footer
.footer {
  flex: 0 0 auto;
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #000;
  .footer__signup {
    @media only screen and (max-width: 992px) {
      max-width: 640px;
      margin: 0 auto;
    }
  }
  .signup__title {
    font-family: $font-kelly;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 4px;
  }
  .form-group {
    margin-bottom: 5px;
  }
  .signup__label {
    margin-bottom: 5px;
    color: #fff;
    font-size: 10px;
    font-weight: 300;
    span {
      color: $yellow;
    }
  }
  input {
    height: 25px;
    max-width: 393px;
    font-size: 14px;
    @media only screen and (max-width: 992px) {
      max-width: 100%;
    }
  }
  .signup__submit {
    margin-top: 15px;
    font-size: 15px;
    text-transform: uppercase;
    font-family: $font-kelly;
    letter-spacing: 4px;
    background-color: $yellow;
    color: #000;
    border: none;
  }
  .social-iconset {
    margin: 0 auto;
    @media only screen and (max-width: 992px) {
      margin: 40px auto 40px;
    }
  }
  .social-icon {
    min-width: 26px;
    width: 26px;
    height: 26px;
    .icon {
      font-size: 19px;
    }
  }
  .footer__content {
    max-width: 640px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
  }
  .footer__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .footer__nav,
  .nav-link {
    padding: 0;
    @include rfs(12);
  }
  .footer__description {
    font-size: 11px;
    color: #fff;
  }
  .footer__pictures {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .footer__picture--logo {
    width: 75px;
    height: 67px;
  }
  .footer__picture--fes {
    width: 76.9px;
    height: 71.71px;
  }
}

// SEARCH PAGE
.search {
  .search__field {
    height: 30px;
    padding-left: 5px;
    color: $grey;
    border: 1px solid $dark-grey;
  }
  .search__select {
    width: 100%;
    option:focus {
      color: #000;
    }
  }
  .search__input-wrap {
    position: relative;
    display: flex;
    align-items: center;
    .search__input-btn {
      position: absolute;
      border: none;
      background-color: transparent;
      .icon-search {
        color: $grey;
        font-size: 17px;
      }
    }
    .search__input-btn--submit {
      top: -2;
      left: 0;
      margin-right: -20px;
    }
    .search__input-btn--clear {
      height: 28px;
      width: 21px;
      right: 1px;
      background-color: $yellow;
      transition: background-color .2s ease;
      &:hover {
        background-color: darken($yellow, 10%);
      }
      .icon-remove {
        display: inline-block;
        width: 6.23px;
        height: 9.37px;
        background-image: url("../img/delete-icon.svg");
      }
    }
  }
  .search__input {
    padding-left: 35px;
    padding-right: 20px;
    box-sizing: border-box;
  }
  // search result
  .search__results {
    width: 100%;
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .search__item-wrap {
    max-width: 280px;
    margin: 0 12px 35px;
    color: #000;
  }
  .search__item {
    &:hover .search__item-desc {
      visibility: visible;
      height: 100%;
    }
  }
  .search__image {
    position: relative;
  }
  .search__item-desc {
    visibility: hidden;
    padding-left: 1%;
    padding-right: 1%;
    margin: 0;
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0px;
    @include rfs(10);
    font-weight: 700;
    text-align: center;
    background-color: rgba(248, 236, 31, 0.85);
    transition: all 0.4s ease;
    overflow: hidden;
  }
  .search__item-caption {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include rfs(15);
  }
  .search__item-left {
    display: flex;
    flex-direction: column;
    color: $grey;
  }
  .search__item-name {
    font-weight: 700;
  }
  .search__item-prize {
    @include rfs(13);
  }
  .search__item-right {
    position: relative;
    max-width: 65px;
    margin-bottom: 0;
    padding: 5px;
    font-family: $font-kelly;
    font-weight: 700;
    line-height: 1;
    @include rfs(10);
    text-align: center;
    border: 2px solid $grey;
    border-radius: 5px;
    background-color: #fff;
    ;
    &:before {
      position: absolute;
      top: -7px;
      right: -7px;
      content: "";
      width: 25px;
      height: 25px;
      background-color: rgba(248, 236, 31, 0.85);
      border-radius: 50%;
      z-index: -1;
    }
  }
}

//  PROGRAM PAGE
.program {
  padding-bottom: 30px;
  .program__days {}
  // day events
  .day__event-list {
    padding: 0;
  }
  .day__event-item {
    margin-bottom: 10px;
    margin-left: -9px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 14px;
    font-weight: 700;
    line-height: 1;
  }
  .day__event-content {
    display: flex;
    margin-left: 31px;
  }
  .day__event-time {}
  .day__event-icon {
    margin-top: 2px;
    position: relative;
    width: 23px;
    &:before {
      content: "";
      position: absolute;
      top: -5px;
      left: 2px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      background-color: $yellow;
      z-index: -1;
    }
    .icon--music {
      display: inline-block;
      margin-top: -4px;
      width: 22.82px;
      height: 25.77px;
    }
    .icon--film {
      width: 23.16px;
      height: 23.16px;
    }
    .icon--mic {
      display: inline-block;
      margin-top: -4px;
      width: 16px;
      height: 28px;
    }
  }
  .day__event-text {
    margin-left: 8px;
  }
  .day__event-name {
    font-size: 15px;
    font-weight: 700;
    letter-spacing: -0.5px;
  }
  .day__event-desc {
    margin-bottom: 0;
    margin-top: -2px;
    font-size: 12px;
    font-weight: 300;
  }
}

// CONTACT PAGE
.contact {
  .slider {
    margin: 0 auto;
    max-width: 1194px;
  }
  .kinokult {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .kinokult__poster {
    img {
      width: 100%;
    }
  }
  .kinokult__subtitle {
    margin-bottom: 23px;
    @include rfs(17);
    text-transform: uppercase;
  }
  .kinokult__paragraph {
    @include rfs(14);
  }
  .map {
    margin-bottom: 25px;
  }
  #map {
    width: 100%;
    height: 500px;
  }
}

// next film
.desc__next {
  margin-top: 100px;
}

.desc__next-title {
  width: 100%;
  padding-left: 10px;
  @include rfs(19);
  font-family: $font-kelly;
  color: #fff;
  letter-spacing: 3px;
  line-height: 1.5;
  background-color: #000;
}

.desc__next-films {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 8px;
}

.desc__next-link {
  max-width: 275px;
  width: 100%;
}

.desc__next-link figure {
  position: relative;
  img {
    width: 100%;
  }
}

.desc__next-caption {
  width: 100%;
  padding: 6px 6px 6px 15px;
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 11px;
  line-height: 1.4;
  color: #fff;
  background-color: rgba(248, 236, 31, 0.9);
  span:first-child {
    text-transform: uppercase;
  }
}

// MOVIE PAGE
.movie {
  margin-bottom: 40px;
  .slider {
    position: relative;
    max-width: 1194px;
    margin: 0 auto;
  }
  .cont-inner {
    padding-top: 50px;
    display: flex;
    @media only screen and (max-width: 768px) {
      display: block;
    }
  }
  .desc {
    padding-right: 10%;
    @media only screen and (max-width: 992px) {
      padding-right: 1%;
    }
  }
  .desc__title {
    display: inline-block;
    margin-left: -109px;
    margin-bottom: 40px;
    padding: 10px 10px 5px;
    @include rfs(22);
    line-height: 1;
    font-family: $font-kelly;
    letter-spacing: 2px;
    color: #ffffff;
    text-transform: uppercase;
    background-color: #000;
    @media only screen and (max-width: 992px) {
      margin-left: 0;
    }
  }
  .desc__subtitle {
    margin-bottom: 40px;
    @include rfs(17);
    text-transform: uppercase;
    line-height: 1.5;
  }
  .desc__text {
    @include rfs(14);
    font-weight: 300;
  }
  // sidebar
  .sidebar__title {
    display: inline-block;
    position: relative;
    margin-bottom: 40px;
    padding: 3px 10px 3px 10px;
    font-family: $font-kelly;
    @include rfs(19);
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-color: $yellow;
    color: #000;
    @media only screen and (max-width: 768px) {
      margin-top: 40px;
    }
    .youtube-icon {
      position: absolute;
      left: 0;
      top: -25px;
      width: 60px;
      height: 60px;
      color: #000;
    }
  }
  .sidebar__play {
    padding-left: 70px;
  }
  .sidebar__table {
    @include rfs(14);
    td {
      vertical-align: top;
    }
  }
  .sidebar__line {
    td {
      padding-bottom: 15px;
    }
    td:first-child {
      padding-right: 10px;
      font-weight: 700;
    }
    td:last-child {
      font-weight: 300;
    }
  }
  .social-iconset--sidebar {
    margin-top: 50px;
    display: flex;
    align-items: center;
    .sidebar__share {
      margin-left: 10px;
      @include rfs(19);
      font-family: $font-kelly;
      font-weight: 700;
      letter-spacing: 3px;
    }
    .social-icon {
      min-width: 40px;
      width: 40px;
      height: 40px;
      background-color: #000;
      .icon {
        @include rfs(30);
        color: $yellow;
      }
    }
  }
}

// GUEST PAGE
// EVENT PAGE
.event {
  .slider__item {
    padding: 55px 1% 65px;
    text-align: center;
    text-transform: uppercase;
    @include rfs(19);
    font-family: $font-kelly;
    color: #fff;
    background-color: #000;
  }
  .slider__item-title {
    @include rfs(40);
  }
  .slider__item-time {
    @include rfs(27.5);
    color: $yellow;
  }
  .slick-prev,
  .slick-next {
    background: rgba(255, 255, 255, 0.2);
  }
  .desc__title {
    margin-bottom: 30px;
    span {
      color: $yellow;
    }
  }
  .desc__subtitle {
    margin-bottom: 20px;
  }
  .desc__video {
    margin-top: 50px;
  }
  .sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .sidebar__title {
    display: block;
    line-height: 1.5;
  }
}

// avatar
.avatars-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  @media only screen and (max-width: 768px) {
    justify-content: space-around;
  }
}

.avatar {
  max-width: 115px;
  text-align: center;
  img {
    width: 100%;
    border-radius: 50%;
  }
}

.avatar__name {
  margin-top: 10px;
  font-family: $font-kelly;
  @include rfs(14);
  font-weight: 600;
  text-transform: uppercase;
  color: #000;
  span {
    display: inline-block;
    padding: 0 8px;
    background-color: $yellow;
  }
}

// SPEAKERS PAGE
.speakers {
  .avatars-wrap {
    width: 100%;
    justify-content: space-around;
  }
  .avatar {
    max-width: 250px;
  }
  .avatar__name {
    margin-top: 25px;
    margin-bottom: 10px;
    @include rfs(17);
    letter-spacing: 2px;
  }
  .avatar__grade {
    color: $grey;
  }
  .social-iconset--speakers {
    justify-content: center;
    .social-icon {
      margin-left: 4px;
      margin-right: 4px;
      background-color: $grey;
      .icon {
        color: $yellow;
      }
    }
  }
}

// GUEST FOTO
.guest {
  .guest__info {
    padding-top: 70px;
    padding-bottom: 70px;
    background-color: #000;
    background-image: url("../img/imprint.png");
    background-repeat: no-repeat;
    background-position: right bottom;
  }
  .guest__topcol {
    display: flex;
    @media only screen and (max-width: 768px) {
      justify-content: center;
      margin-bottom: 20px;
    }
  }
  .guest__topcol--foto {
    @media only screen and (max-width: 768px) {
      justify-content: center!important;
    }
  }
  .guest__foto {
    right: 30px;
    display: flex;
    justify-content: center;
    position: relative;
    max-width: 250px;
    min-width: 150px;
    &:before {
      position: absolute;
      top: -10px;
      right: -10px;
      content: "";
      width: 40%;
      height: calc(100% + 20px);
      background-color: $yellow;
      z-index: 0;
    }
    img {
      width: 100%;
      height: 100%;
      z-index: 2;
    }
  }
  .guest__name-wrap {
    padding: 0 10%;
    font-family: $font-kelly;
    @include rfs(18);
    color: #fff;
    text-transform: uppercase;
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
  }
  .guest__name {
    @include rfs(50);
    color: $yellow;
  }
  .social-iconset--guest {
    @include rfs(45);
    .social-icon {
      width: 1.2em;
      height: 1.2em;
    }
    .icon {
      @include rfs(42);
    }
  }
  // text
  .guest__text {
    padding-left: 7%;
    @media only screen and (max-width: 768px) {
      padding-left: 0;
    }
  }
  // sidebar
  .guest__speech {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .guest__sidebar {
    max-width: 250px;
  }
  .day {
    padding-left: 20px;
  }
  .day__numeric {
    font-size: 13.7px;
    letter-spacing: 0;
    text-transform: none;
  }
  .guest__sidebar-desc {
    @include rfs(14);
    font-weight: 700;
  }
}

// NEWS PAGE
.news {
  .news__banner-wrap {
    display: flex;
    min-height: 200px;
    background-image: url('../img/news__banner-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
  }
  .news__banner {
    display: flex;
    justify-content: flex-end;
    @media only screen and (max-width: 768px) {
      justify-content: center;
    }
  }
  .news__heading {
    padding: 50px 100px;
    background-image: url("../img/news__title-bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center;
    span {
      white-space: nowrap;
      @include rfs(47);
      font-family: $font-kelly;
      text-transform: uppercase;
      line-height: 1;
      border-bottom: 10px solid $yellow;
    }
  }
  .news__main {
    padding-top: 100px;
    .content-title {
      display: block;
      margin-bottom: 30px;
    }
  }
  .news__links-box {
    @media only screen and (min-width: 992px) {
      padding-left: 13%;
    }
  }
  .news__link {
    display: block;
    margin-bottom: 15px;
  }
  // sidebar
  .news__sidebar {
    max-width: 300px;
  }
  .news__feed-item {
    margin-bottom: 60px;
    figure {
      position: relative;
    }
  }
  .desc__next-image {
    width: 100%;
  }
  .desc__next-caption {
    text-transform: uppercase;
    @include rfs(15);
    font-weight: 500;
    box-sizing: border-box;
  }
  .news__feed-date {
    margin-bottom: 0.5rem;
    @include rfs(10);
    font-family: $font-kelly;
    color: $grey;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  .news__feed-text {
    @include rfs(13);
    a {
      font-weight: 700;
    }
  }
  .social-iconset--feed {
    color: $grey;
    margin-top: 10px;
    .social-icon {
      width: 16px;
      height: 16px;
      background-color: $grey;
      margin-right: 3px;
      margin-left: 3px;
      &:first-child {
        margin-left: 0;
      }
    }
    .icon {
      color: #fff;
      font-size: 11px;
    }
    .social-iconset-share {
      @include rfs(13);
      margin-left: 5px;
      font-family: $font-kelly;
    }
  }
}

@import "media";